:root {
  --font-family: 'Manrope', sans-serif;
}

body {
  background: #0b0c10;
  padding: 0px;
  margin-left: 0px;
  margin-right: 0px;

}

.contact-container {
    position: fixed;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-self: center;
    left: 0;
    top: 200px;
}

.contact-icon {
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;

}

.contact-icon:hover {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    box-shadow: 1px #66fcf1;
}

.contact-icon-linkedin {
    transition: -webkit-transform 0.3s ease-in-out;
    transition: transform 0.3s ease-in-out;
    transition: transform 0.3s ease-in-out, -webkit-transform 0.3s ease-in-out;
}

.contact-icon-linkedin:hover {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}

.contact-icon-email {
    transition: -webkit-transform 0.3s ease-in-out;
    transition: transform 0.3s ease-in-out;
    transition: transform 0.3s ease-in-out, -webkit-transform 0.3s ease-in-out;
}

.contact-icon-email:hover {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}

.contact-icon-github {
    transition: -webkit-transform 0.3s ease-in-out;
    transition: transform 0.3s ease-in-out;
    transition: transform 0.3s ease-in-out, -webkit-transform 0.3s ease-in-out;
}

.contact-icon-github:hover {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}
.navbar-container {
    background: #1f2833;
    height: 50px;
    width: 100%;
    position: fixed;
    left: 0px;
    top: 0px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    z-index: 5;
}

.navbar-links-container {
    font-family: var(--font-family);
    display: flex;
    justify-content: space-around;
    grid-column-gap: 25px;
    -webkit-column-gap: 25px;
            column-gap: 25px;
}

.navbar-links-container a {
    font-weight: 500;
    font-size: 16px;
    margin: 0px;
    text-transform: capitalize;
    text-decoration: none;
    color: #66fcf1;
    padding: 5px 5px;
    margin: 5px 0px;
    justify-content: center;
    width: 100px;
    letter-spacing: .1em;
}

.navbar-container-menu a {
    font-weight: 500;
    font-size: 16px;
    margin: 0px;
    text-transform: capitalize;
    text-decoration: none;
    color: #66fcf1;
    padding: 5px 5px;
    margin: 5px 0px;
    justify-content: center;
    width: 100px;
    letter-spacing: .1em;
}

.navbar-logo-in {
    font-weight: 1000;
    font-size: 30px;
    text-transform: capitalize;
    text-decoration: none;
    color: #66fcf1;
}

.navbar-container-menu a:hover {
    cursor: pointer;
    background: #66fcf1;
    text-decoration: none;
    color: #ffffff;
    border-radius: 100px;
    text-shadow: 0 1px 1px 1px#000000, 0 1px 1px 1px #040404;
    box-shadow: 0px 2px 2px 2px#45A29E;
}

.navbar-links-container a:hover {
    cursor: pointer;
    background: #66fcf1;
    text-decoration: none;
    color: #ffffff;
    border-radius: 100px;
    text-shadow: 0 1px 1px 1px#000000, 0 1px 1px 1px #040404;
    box-shadow: 0px 2px 2px 2px#45A29E;
}

.navbar-menu_container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    text-align: center;
    background: #1f2833;
    position: absolute;
    right: 0;
    top: 40px;
    min-width: 100px;
    border-radius: 5px;
}

.navbar-menu-container-links a {
    text-decoration: none;
    color: #66fcf1;
    padding: 5px 5px;
    margin: 5px 0px;
    justify-content: center;
    width: 100px;
    letter-spacing: .1em;
    cursor: pointer;
}

.navbar-menu {
    cursor: pointer;
    position: relative;
    display: none;
}

.navbar-menu-container-links a:hover {
    background: #66fcf1;
    text-decoration: none;
    color: #ffffff;
    border-radius: 100px;
    text-shadow: 0 1px 1px 1px#000000, 0 1px 1px 1px #040404;
    box-shadow: 0px 2px 2px 2px#45A29E;
}

.narbar-logo {
    text-decoration: none;
}

@media screen and (max-width: 700px) {

    .navbar-links-container {
        grid-column-gap: 15px;
        -webkit-column-gap: 15px;
                column-gap: 15px;
    }

    .navbar-links-container {
        display: none;
    }

    .navbar-menu {
        display: block;
    }
}
.home-container {
    width: 100%;
    height: 100%;
    padding: 50px 0px;
    font-family: var(--font-family);
    display: flex;
    flex-direction: column;
    color: #fff;
    background: linear-gradient(to right, #0b0c10, #1f2833, #45A29E);
    background-size: 200% 200%;
    -webkit-animation: gradient 10s ease infinite;
            animation: gradient 10s ease infinite;
    align-items: center;
    justify-content: center;
}

.home-name-image {
    display: flex;
    justify-content: space-around;
}

.home-tagline {
    color: #c5c6c7;
}

.home-image-one {
    width: 85px;
    height: 130px;
    border: 5px solid transparent;
    border-image: linear-gradient(to right, #1f2833, #45A29E) 1;
    background-color: transparent;
}


.home-learn-more {
    color: #c5c6c7;
    font-weight: 700;
}

.home-greeting-name span {
    display: inline-block;
    width: -webkit-max-content;
    width: max-content;
    -webkit-animation: typing 3s steps(28, end) forwards;
            animation: typing 3s steps(28, end) forwards;
    white-space: nowrap;
    overflow: hidden;
    border-right: 2px solid;
}

.home-greeting-name-container {
    display: flex;
    justify-content: left;
    align-items: center;
    margin: 0px 10px;
}


@-webkit-keyframes gradient {
    0% {
        background-position: 0% 50%;
    }

    50% {
        background-position: 100% 50%;
    }

    100% {
        background-position: 0% 50%;
    }
}


@keyframes gradient {
    0% {
        background-position: 0% 50%;
    }

    50% {
        background-position: 100% 50%;
    }

    100% {
        background-position: 0% 50%;
    }
}


@-webkit-keyframes typing {
    from {
        width: 0;
    }

    to {
        width: 100%;
    }
}


@keyframes typing {
    from {
        width: 0;
    }

    to {
        width: 100%;
    }
}



@media screen and (max-width: 700px) {
    .navbar-links-container {
        display: none;
    }

    .home-greeting-name span {
        flex-wrap: nowrap;
        text-align: center;
        font-size: 30px;
        width: -webkit-max-content;
        width: max-content;
        margin: 0px 5px;
    }

    .home-occupation {
        margin: 0px 5px;
    }


    .navbar-menu {
        display: inline;
    }

    .home-tagline-container {
        margin: 0px 5px;
    }
}

@media screen and (max-width: 450px) {

    .home-greeting-name span {
        flex-wrap: nowrap;
        text-align: center;
        font-size: 25px;
        width: -webkit-max-content;
        width: max-content;
    }

    .home-greeting-name-container {
        justify-content: center;
    }

    .home-occupation {
        flex-wrap: nowrap;
        text-align: center;
        font-size: 25px;
        display: flex;
        justify-content: center;
    }

    .home-tagline-container {
        flex-wrap: nowrap;
        text-align: center;
        font-size: 18px;
        display: flex;
        justify-content: center;
    }

    .home-learn-more {
        font-size: 18px;
    }
}


@media screen and (max-width: 350px) {

    .home-greeting-name span {
        flex-wrap: nowrap;
        text-align: center;
        font-size: 20px;
        width: -webkit-max-content;
        width: max-content;
    }


    .home-occupation {
        flex-wrap: nowrap;
        text-align: center;
        font-size: 16px;
        display: flex;
        justify-content: center;
    }

    .home-tagline-container {
        flex-wrap: nowrap;
        text-align: center;
        font-size: 12px;
        display: flex;
        justify-content: center;
    }

    .home-learn-more {
        font-size: 12px;
    }
}
.about-container {
    width: 100%;
    height: 100%;
    padding: 50px 0px;
    font-family: var(--font-family);
    display: flex;
    flex-direction: column;
    color: #fff;
    background: transparent;
    align-items: center;
    justify-content: center;
}

.about-bio {
    color: #c5c6c7;
    font-family: var(--font-family);
    font-size: 12px;
    box-sizing: border-box;
    padding: 0px 10px;
}

.about-header {
    color: #fff;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.about-skills {
    color: #66fcf1;
    white-space: nowrap;
    font-weight: 700;
}

.about-hr {
    background: #45a29e;
    margin: 0px 10px;
    width: 150px;
    height: 10px;
}

.about-bio-container {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0px 100px;
}

.about-bio-paragraphs {
    display: flex;
    flex-direction: column;
}

.about-bio-image {
    width: 200px;
    height: 170px;
    border: 5px solid transparent;
    margin: 5px;
    border-image: linear-gradient(to right, #45A29E, #1f2833) 1;
    background-color: transparent;
}


.about-bio-image2 {
    margin: 5px;
    width: 200px;
    height: 170px;
    border: 5px solid transparent;
    border-image: linear-gradient(to right, #45A29E, #1f2833) 1;
    background-color: transparent;
    display: none;
}

.about-bio-container-image:hover .about-bio-image {
    display: none;
}

.about-bio-container-image:hover .about-bio-image2 {
    display: block;
}

@media screen and (max-width: 700px) {
    .about-bio-container {
        flex-direction: column-reverse;
    }

    .about-header span {
        font-size: 20px;
    }

    .about-bio-container {
        padding: 0px;
    }

    .about-hr {
        width: 100px;
    }
}



@media screen and (max-width: 450px) {
    .about-hr {
        width: 70px;
    }
}

@media screen and (max-width: 350px) {
    .about-header span {
        font-size: 16px;
    }

    .about-bio-container {
        padding: 0px
    }

    .about-hr {
        width: 30px;
    }
}

@media screen and (max-width: 250px) {
    .about-hr {
        display: none;
    }

}
